const SweetAlert = require('sweetalert2');

export const successAlert = (message, title = 'Envo exitoso!') =>
  SweetAlert.fire({
    title,
    text: message,
    icon: 'success',
    confirmButtonText: 'Ok',
  });

export const errorAlert = (message, title = 'Algo salio mal!') =>
  SweetAlert.fire({
    title,
    text: message,
    icon: 'error',
    confirmButtonText: 'Cerrar',
  });
